<!-- eslint-disable linebreak-style -->
<!-- eslint-disable max-len -->
<template>
  <ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <a href="#" class="pagination__link pagination__link--arrow" v-if="page !== 1" :class="{'pagination__link--disabled': page === 1}" aria-label="Предыдущая страница" @click.prevent="paginate(page - 1)">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">
              {{ pageNumber }}
            </a>
          </li>
          <li class="pagination__item">
            <a href="#" class="pagination__link pagination__link--arrow" v-if="page !== pages" :class="{'pagination__link--disabled': page === pages}" aria-label="Следующая страница" @click.prevent="paginate(page + 1)">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
        </ul>
</template>

<script>
export default {
  props: ['modelValue', 'count', 'perPage'],
  computed: {
    page() {
      return this.modelValue;
    },
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('update:modelValue', page);
    },
  },
};
</script>
